import React from 'react';
import {connect} from "react-redux";
import Profile from "./Profile";
import {getUserProfile, drawLoad, owner, savePhoto, saveProfile, getStatus, updateStatus} from "../../redux/profile-reducer";
import {logout} from "../../redux/auth-reducer";
import {withRouter} from "react-router-dom";

class Profiles extends React.Component {
    refreshProfile() {
        let userId = this.props.match.params.userId;
        if (this.props.userId === Number(this.props.match.params.userId)) {
            this.props.owner(true)
        } else {
            this.props.owner(false)
        }
        if (this.props.id === undefined) {
            this.props.getUserProfile(userId);
            this.props.getStatus(userId);
        }
    }
    componentDidMount() {
        this.refreshProfile();
    };
    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.match.params.userId !== prevProps.match.params.userId) {
            this.refreshProfile();
        }
    }
    render() {
        return <Profile {...this.props} saveProfile={this.props.saveProfile} savePhoto={this.props.savePhoto} logout={this.props.logout}/>
    }
}

let mapStateToProps = (state) => {
    return {
        profilePage: state.profilePage,
        userId: state.auth.id,
        isAuth: state.auth.isAuth,
        id: state.profilePage.profile.data.id
    }
};

export default connect(mapStateToProps, {getUserProfile, drawLoad, owner, savePhoto, logout, saveProfile, updateStatus, getStatus})(withRouter(Profiles));
