import React from "react";
import style from "./users.module.css";
import '../../App.css';
import User from "./User/User";
import Paginator from "./Pagination/Paginator";
import {Redirect} from "react-router-dom";

const Users = (props) => {
    if (props.users.length === 0) {
        return <div></div>
    }

    if (props.click) {
        return (
            <Redirect to={"/profile/" + props.userId}/>
        )
    }

    return (
        <div className="container">
            <div className={style.general}>
                <h1 className={style.title}>Ищи друзей среди {props.totalUserCount} пользователей</h1>
                <div className={style.items}>
                    {props.users.map(u => <User key={u.id} id={u.id} photo={u.photos.large}
                                                name={u.name} followed={u.followed} getProfilePage={props.getProfilePage}
                                                isFollow={props.isFollow} isUnFollow={props.isUnFollow} disable={props.disable} buttonDisable={props.buttonDisable}/>)}
                </div>
                <Paginator onPageChanged={props.onPageChanged} totalUserCount={props.totalUserCount}
                           pageSize={props.pageSize} currentPage={props.currentPage}/>
            </div>
        </div>
    )
};

export default Users;
