// @ts-ignore
import {authAPI} from "../api/api";

const SET_AUTH_USER_DATA = 'auth/SET_AUTH_USER_DATA';
const UN_REGISTER = 'auth/UN_REGISTER';
const GET_CAPTCHA_URL_SUCCESS = 'auth/GET_CAPTCHA_URL_SUCCESS';

export let initialState = {
    id: 0,
    email: null,
    login: null,
    isAuth: false,
    captchaParam: null
};

const authReducer = (state=initialState, action:any) => {
    switch (action.type) {
        case SET_AUTH_USER_DATA:
            return {
                ...state,
                ...action.data
            }
        case UN_REGISTER:
            return {
                ...state
            }
        case GET_CAPTCHA_URL_SUCCESS:
            return {
                ...state,
                captchaParam: action.url
            }
        default:
            return state;
    }
};

export const setAuthUserData = (id:any, email:any, login:any, isAuth:any) => ({type: SET_AUTH_USER_DATA, data: {id, email, login, isAuth}});

export const getCaptchaUrlSuccess = (url:any) => ({type: GET_CAPTCHA_URL_SUCCESS, url});

export const getMe = () => async (dispatch:any) => {
    let response = await authAPI.getMe()
    if (response.data.resultCode === 0) {
        let {id, email, login} = response.data.data
        dispatch(setAuthUserData(id, email, login, true));
    }
    return response
}

export const captchaUrl = () => async (dispatch:any) => {
    const response = await authAPI.captcha();
    const captchaUrl = response.data.url;
    dispatch(getCaptchaUrlSuccess(captchaUrl))
}

export const signInThunk = (email:any, password:any, rememberMe:any, captcha:any) => async (dispatch:any) => {
    let response = await authAPI.signInAPI(email, password, rememberMe, captcha)
    if (response.resultCode === 0) {
        dispatch(getMe())
    } else if (response.resultCode === 10) {
        dispatch(captchaUrl())
    }
    return response
}

export const logout = () => async (dispatch:any) => {
    let response = await authAPI.logout()
    if (response) {
        dispatch(setAuthUserData(null, null, null, false))
    }
}


export default authReducer;
