import React from "react";
import {connect} from "react-redux";
import {
    follow,
    unfollow,
    setUsers,
    setCurrentPage,
    setTotalUsersCount,
    getUsers,
    changeUsers,
    isFollow,
    isUnFollow,
    updateCurrentPage,
    buttonDisable
} from "../../redux/user-reducer";
import {getUserProfile, getStatus} from '../../redux/profile-reducer'
import Users from "./Users";

class UsersCont extends React.Component {
    state = {
        click: false
    }

    componentDidMount() {
        this.props.getUsers(this.props.currentPage, this.props.pageSize)
    };

    getProfilePage = (userId) => {
        this.props.getStatus(userId);
        this.props.getUserProfile(userId).then(() =>{
            this.setState({click: true})
        })
    }

    onPageChanged = (p) => {
        this.props.changeUsers(p, this.props.pageSize);
    };

    render() {
        return <Users {...this.props} click={this.state.click} onPageChanged={this.onPageChanged} getProfilePage={this.getProfilePage}/>
    }
}

let mapStateToProps = (state) => {
    return {
        users: state.usersPage.users,
        pageSize: state.usersPage.pageSize,
        totalUserCount: state.usersPage.totalUserCount,
        currentPage: state.usersPage.currentPage,
        isFetching: state.usersPage.isFetching,
        userId: state.profilePage.profile.data.userId,
        disable: state.usersPage.disable
    }
};

const UsersContainer = connect(mapStateToProps, {
    follow, unfollow, setUsers, setCurrentPage,
    setTotalUsersCount, getUsers, changeUsers,
    isFollow, isUnFollow, updateCurrentPage, getUserProfile, getStatus, buttonDisable})(UsersCont);

export default UsersContainer;
